import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { ShippingsService } from '../shippings.service';
import { ToastrService } from 'ngx-toastr';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';

import { PlacesService } from '../../places/places.service';
import { InputSelectItem } from 'src/app/models/InputSelectItem';

@Component({
  selector: 'app-add-shipping',
  templateUrl: './add-shipping.component.html',
  styleUrls: ['./add-shipping.component.scss'],
})
export class AddShippingComponent implements OnInit {
  image;
  constructor(
    private fb: FormBuilder,
    private shippingService: ShippingsService,
    private placesService: PlacesService,
    private toastr: ToastrService,
    private spinner: NgxSpinnerService,
    private router: Router
  ) {}

  form = this.fb.group({
    from: ['', Validators.required],
    to: ['', Validators.required],
    cost: [0, Validators.required],
  });

  ngOnInit(): void {
    this.getPlacesList();
  }

  // Start select dropdown
  selectPlacesList: InputSelectItem[];
  selectedFromPlaceId: string;
  selectedToPlaceId: string;
  getPlacesList() {
    this.spinner.show();

    this.placesService.getAll();

    this.placesService.places.subscribe((data) => {
      if (data) {
        this.selectPlacesList = data.map((x) => {
          return {
            _id: x['_id'],
            nameEn: `${x['name']}`,
            nameAr: ``,
          };
        });
      }
    });

    this.spinner.hide();
  }
  selectFromPlace(event) {
    if (event) {
      this.selectedFromPlaceId = event._id;
      this.form.get('from').patchValue(event.nameEn);
    }
    console.log(this.form.value);
  }
  selectToPlace(event) {
    if (event) {
      this.selectedToPlaceId = event._id;
      this.form.get('to').patchValue(event.nameEn);
    }
    console.log(this.form.value);
  }
  // End select dropdown

  onSubmit(formValue) {
    this.spinner.show();

    this.shippingService.addOne(formValue).subscribe(
      (res) => {
        this.image = null;
        this.form.reset();
        this.toastr.info(res['message'], 'Info');
        this.router.navigate(['/shippings']);
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
    this.spinner.hide();
  }
}
