import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ShippingsComponent } from './shippings.component';
import { AddShippingComponent } from './add-shipping/add-shipping.component';

const routes: Routes = [
  { path: '', component: ShippingsComponent },
  { path: 'add', component: AddShippingComponent },
];
@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class ShippingsRoutingModule {}
