import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject } from 'rxjs';

import { baseUrl } from '../../config/variables';

@Injectable({
  providedIn: 'root',
})
export class ShippingsService {
  constructor(private http: HttpClient) {}

  shippings = new BehaviorSubject<[]>(null);

  getAll() {
    return this.http.get(baseUrl + '/shipping').subscribe(
      (res) => {
        if (res['data']) {
          this.shippings.next(res['data']);
        }
      },
      (error) => {
        console.log(error);
        // this.error.next(error.error.message);
      }
    );
  }

  getOne(itemId) {
    return this.http.get(baseUrl + `/shipping/${itemId}`);
  }

  addOne(form) {
    return this.http.post(baseUrl + '/shipping', form);
  }

  updateOne(form, itemId) {
    return this.http.put(baseUrl + `/shipping/${itemId}`, form);
  }

  deleteOne(itemId) {
    return this.http.delete(baseUrl + `/shipping/${itemId}`);
  }
}
